@font-face {
    font-family: 'livvic-Bold';
   src: url('./fonts/Livvic-Bold.ttf')
}
@font-face {
    font-family: 'livvic';
   src: url('./fonts/Livvic-SemiBold.ttf')
}



* {
    box-sizing: border-box;
    font-family:livvic;
    color: #ffffff;
}
li{
    list-style: none;
}
body{
    margin:0;
   
}
.App{
    background-color: #004047;
}
.container {
    max-width: 1060px;
    margin: 0 auto;
    padding-left: 5px;
    padding-right: 50px;
}

ul{
    width: 100%;
}
/* helpers */
.bold{
    font-family:livvic-Bold
}
.flex {
    display: flex;
}
.flex-row{
    flex-direction: row;
}
.flex-col{
    flex-direction: column;
}
.flex.wrap {
    flex-wrap: wrap;
}
.align-center {
    align-items: center;
}
.text-center{
    text-align: center;
}
.flex-start{
    justify-content: flex-start;
}
.grid{
    display: grid;
}
.full-height{
    height: 100%;
}
.space-between {
    justify-content: space-between;
}
.space-evenly{
    justify-content: space-evenly;
}
.relative{
    position: relative;
}
.orange-text{
    color: #f67e7e;
}
.first-section{
    background-color: #004047;
    height: 100vh;
}
.second-section{
    background-color: #012f34;
        height: 100vh;
        position: relative;
}
.third-section{
    background-color: #004047;
   height: 100vh;;
}
.four-section{
    height: 200px;
    background-color: #f67e7e;
    position: relative;
}

.footer{
background-color: #002529;
}
.delivery-section{

    padding-top: 100px
}


.delivery-section .details{
grid-template-columns: repeat(3,1fr);
grid-gap: 50px;
}
.info{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-top: 35px;
}
.info p{
    text-align: center;
    padding-right: 10px;
color: #ffffff;
font-size: 15px;
font-weight: 600;
line-height: 25px;
z-index: 10;
}
.info .avatar{
    width: 70px;
    height: 70px;
    border-radius: 50%;
    margin-top: 20px;
}
.info .name{
    color: #79c8c7;
font-family: "Livvic - Bold";
font-size: 18px;
font-weight: 700;
line-height: 28px;
}
.info .job{
    color: #ffffff;
font-family: "Livvic - Medium Italic";
font-size: 13px;
font-weight: 500;
line-height: 18px;
}
.delivery-section .main-title{
    color: #ffffff;
font-size: 48px;
font-weight: 700;
line-height: 48px;
padding-left: 70px;
}
.delivery-section .green-text{
    color: #79c8c7;
}
.quotes{
    width: 67px;
    height: 56px;
    position: absolute;
    margin-top: -125px;
}
.nav-btn{
    width: 153px;
    height: 48px;
    border-radius: 24px;
    color: white;
    background: transparent;
    border: 2px solid #ffffff;
    font-size: 18px;
    font-weight: 600;
    line-height: 28px;
}
.navbar{
    padding-top: 60px;
    height: 10%;
}
.nav li{
    list-style: none;
    margin-right: 80px;
    color: #ffffff;
    font-family: "Livvic - Semi Bold";
    font-size: 18px;
    font-weight: 600;
}
.nav li:last-child{
    position: absolute;
    right: 0;
}
.bg-pattern{
    position: absolute;
    left: 0;
}
.bg-pattern-home{
    position: absolute;
    right: 0;
    bottom: 0;
}
.talent-img1{
    position: absolute;
    left: -100px;
    top: 40%
}
.talent-img2{
    position: absolute;
    bottom: 0;
    right: 150px;
}

/* first section */
.talent-section{
    height: 90%;
}
.talent-section .main{
    display: grid;
    grid-template-columns: 60% 40%;
    margin-left: 35px;
}

.talent-section .main-title{
    font-size: 90px;
    color: #ffffff;
    line-height: 100px;
    z-index: 10;
}
.talent-section p{
    height: 105px;
    color: #ffffff;
    font-size: 18px;
    line-height: 28px;
}
.talent-section .rec{
    width: 50px;
    height: 4px;
    background-color: #79c8c7;
    position: absolute;
    top: 40%;
}

.contact-section .main-title{
color: #012f34;
font-size: 48px;
line-height: 48px;
z-index: 10;
margin-left: 100px;
}
.contact-btn{
    width: 153px;
    height: 48px;
    border-radius: 24px;
    color: #012f34;
    background: transparent;
   border: 2px solid #012f34;
    font-size: 18px;
    font-weight: 600;
    line-height: 28px;
}
.contact-section img{
    position: absolute;
    bottom: 0;
    left: 0;
}
.footer-section{
    
    padding: 40px;
}
.footer .main{
    display: grid;
    width: 100%;
    grid-template-columns: 30% 30% 50%;
    padding: 25px;
    gap:20px;
}
.footer li{
    list-style: none;
    display: inline-block;
    margin-left: 20px;
}

.footer-section .main > div{
    padding: 10px;
}
.social-icons img{
    margin-left: 20px;
}
.address > div{
    opacity: 0.6;
    color: #ffffff;
    font-size: 15px;
    line-height: 25px;
}
.copy-rights{
opacity: 0.6;
font-size: 15px;
line-height: 25px;
}

.second-section .main{
    display: grid;
    grid-template-columns: 40% 60%;
    padding-top:170px;
    gap:30px
}
.second-section .main-title{
   
font-size: 48px;
font-weight: 700;
line-height: 48px
}
.details-texts{
    margin-left: 10px;
}
.details-texts p{
opacity: 0.8;
font-size: 15px;
font-weight: 600;
line-height: 25px;
}
.second-section .bg{
    position:absolute;
    right: 0;
    bottom: 0;
}